import keyboard from '../../components/keyboard/index.vue'
import Toast from '../../wxcomponents/vant/toast/toast.js';
import Dialog from '../../wxcomponents/vant/dialog/dialog.js';
export default {
	components: {
		keyboard
	},
	data() {
		return {
			// 输入的金额
			money: '',
			// 是否点击输入
			isActive: false,
			// 商家详情
			shopDetail: {}
		}
	},
	onLoad: function() {
		if (uni.getStorageSync('selectShopDetail')) {
			this.shopDetail = uni.getStorageSync('selectShopDetail')
		} else {
			Dialog.alert({
				title: '提示',
				message: '请先选择商铺'
			}).then(() => {
				uni.switchTab({
					url: '../index/index'
				})
			})
		}
	},
	onShow: function() {
		if (uni.getStorageSync('selectShopDetail')) {
			this.shopDetail = uni.getStorageSync('selectShopDetail')
			Dialog.close()
		} else {
			Dialog.alert({
				title: '提示',
				message: '请先选择商铺'
			}).then(() => {
				uni.switchTab({
					url: '../index/index'
				})
			})
		}
	},
	methods: {
		getMoney: function(e) {
			this.money = e
		}
	}
}
